
import { getToken, onMessage } from "@firebase/messaging";
import { defineComponent } from "@vue/runtime-core";
import { vapidKey } from "./firebaseConfig";
import { UserService } from "./services/UserService";
import { store } from "./store";
import { LISTEN_USER_AUTH } from "./store/modules/firebaseModule";

export default defineComponent({
  async mounted() {
    // const app = FirebaseService.getApp();
    //useStore(key).commit(SET_APP, app);
    store.dispatch(LISTEN_USER_AUTH);

    const userService = new UserService();
    const currentToken = await this.initMessages();
    if (currentToken) {
      await userService.saveNoticationToken(currentToken);
    }
    onMessage(this.$messaging, (payload) => console.log(payload));
  },
  methods: {
    async initMessages() {
      try {
        /* const reg = await navigator.serviceWorker.register(
          `${process.env.BASE_URL}firebase-messaging-sw.js`
        ); */
        const currentToken = await getToken(this.$messaging, {
          vapidKey: vapidKey,
          // serviceWorkerRegistration: reg,
        });
        if (currentToken) {
          console.log("client token", currentToken);
          return currentToken;
        } else {
          console.log(
            "No registration token available. Request permission to generate one."
          );
        }
      } catch (error) {
        console.error("An error occurred while retrieving token. ", error);
      }
    },
  },
});
