import { RootState, store } from "@/store";
import { Store } from "vuex";
import { FirebaseDBService } from "./FirebaseDBService"

export class UserService {

    private db: FirebaseDBService;
    private store: Store<RootState>;

    constructor() {
        this.db = new FirebaseDBService();
        this.store = store;
    }

    async saveNoticationToken(token: string) {
        const userUid = this.store.state.firebase.user?.uid;
        const notificationTokens = this.store.state.appState.user?.notificationTokens;
        console.log(notificationTokens);
        if (userUid && !(notificationTokens && Object.keys(notificationTokens).includes(token))) {
            const updates = {
                [`/users/${userUid}/notificationTokens/${token}`]: true
            }
            console.log(updates);
            try {
                await this.db.updateValues(updates);
            } catch (error) {
                console.error(error);
            }
        }
    }

}